    {
        title: 'Create Character Concepts',
        paragraphs: [
            `First, choose one of the four Roles from below and write it down in the middle of your index card (if you're using the game-box, then all the players write their Concepts as indicated on the Crew card), with room on both sides to add additional words before and after your Role. Your Role is the reason the Crew decided to bring you alongit represents what youre good at, what makes you useful.`,
            `Con: deception, forging documents, disguises, getting information out of people, general persuasion and charisma`,
            `Burglar: climbing over/around/on top of things, picking locks, sleight of hand, pickpocketing, general athletics`,
            `Hacker: cameras, alarms, elevators, emails, codes/passwords, general intelligence and wisdom`,
            `Muscle: punching stuff, jumping, driving, shooting, general strength`,
            `Next, add another layer to your character with a single adjective tacked onto the front of your Role. While your Role reflects what you do during the game, the adjective you choose will be reflected in how you play and should impact the way you act as your character. Keep it spontaneous and just write down the first adjective that pops into your head! Here are some examples to get you thinking:`,
            `Finally, add the word, with after your Role, and follow that up with a single noun. This could be an object, a relationship, a physical, emotional, or mental condition, or anything else that adds a unique aspect to your character. The adjective, just like the noun, doesn't impact the mechanics of the game, it's purely to make your character more fun and interesting to play. And your Concept isn't meant to capture everything about your character, just some fun details. So dont worry about whether it will be applicable to the heist.`,
            `Here are some examples of complete character concepts:`,
            `Snarky Con with a daughter`,
            `Loyal Burglar with an old photograph`,
            `Distractible Hacker with something to prove`,
            `Cheerful muscle with a chronic cough`,
            `Once everyones finished their Character Concept, make sure you also each take two dice from the smaller pile. These are your Skill Dice, which will be discussed later on. Each player can have a unique pair of Skill Dice, if desired, the important part is that theyre distinct from the main pile of dice, your Luck Dice.`,
        ]
    },
    {
        title: `Crewleader & Scribe`,
        paragraphs: [
            `Once everyone has written down their Concepts, the Crew should nominate a Crewleader and a Scribe. The Crewleaders responsible for taking charge when planning and making split-second decisions during the heist, while the Scribe takes notes during the heist.`
        ],
        subsections: [
            {
                title: 'Choosing a Crewleader',
                paragraphs: [
                    `The game runs more smoothly if the Crewleader is a player who is themselves a natural leader. Players need to get used to turning to their leader when they have a suggestion or would like to try something, rather than turning to you, the GM. You generally go through the leader, not giving something a turn unless he gives the go ahead.`,
                    `If no one is nominated to be Crewleader, suggest a player who you believe would feel comfortable and confident taking charge. But if you do get into the game and the leader isnt acting assertively, gently remind him or her by asking, So, what do you do next? Additionally, if you notice a particular player being given fewer turns, help bring them into the spotlight by suggesting a way their skill could prove useful in the given circumstances.`,
                ]
            },
            {
                title: `Tips for the Crewleader`,
                paragraphs: [
                    `On the first loop, youll almost certainly feel that there you dont have enough dice to make them last the whole hour. Thats okay! Keep in mind that youll have two more times to get everything right, so for that first loop, just learn what you can and enjoy it as everything spins out of control.`,
                ]
            },
            {
                title: `Tips for the Scribe`,
                paragraphs: [
                    `The Scribe takes notes of the timestamps for key events during the heist. The most essential timestamps to record are for events that happen without your crews intervention, like people arriving on the scene from somewhere else, or a shift change. Another thing the scribe will need to keep track of is previous attempts at challenges. Make a note to remind yourself what specific challenge was attempted. Make it specific enough that youll recall the context, so for the Burglars roll to sneak past the security camera at the gate, rather than writing just sneak, perhaps jot down, sneakgate camera. Including timestamps in your challenge notes can help in keeping track of this. Also note whether the attempt was successful or not, as this affects the advantage.`
                ]
            }
        ]
    },
    {
        title: 'Pacing the Time Loops',
        paragraphs: [
            `In a game of Shenan-Again, the Crew uses a Coil, a piece of mysterious extraterrestrial technology that can bend the space-time continuum, to pull off a challenging heist.`,
            `The Coil creates a time loop. If youve seen Groundhog Day, youve already got a pretty good grasp on how this works. The difference here is instead of living the same day over and over, you repeat just one hour, and you only live through that hour three times in total. The last time is the final loop, the one that sticks.`,
            `Once the Coil is activated, theres exactly one hour until the loop resets. As soon as that hour is up, the Crew finds themselves back where they began, in time and space. No one else will have any memory of what happened.`,
            `Thats when the second loop begins. Again at the end of the second loop, everything resets to the exact moment when the Coil was activated, and the hour begins again. The third loop is different, however, because instead of resetting after one hour, time continues to flow normally. The third loop is the final loop, so thats when players need to make sure to pull off the heist. Doesnt matter if they pull it off the first time or the second time, everything will reset and theyll have to do it all over again. Players have to get it right on the third loop.`,
            `The first loop will mostly be a learning experience. During the second loop, players will start to solidify their plan. It's in the third and final loop that everything will (hopefully) come together. At the beginning of the third loop, you'll still set a timer for one hour, but this time, when that third timer goes off, the loop doesn't reset. Instead, if the Crew hasn't successfully pulled off the heist before that third timer goes off, they get caught.`,
            `Each one-hour loop should build on the last one. Although theyre living the same hour each time, for the most part things should happen differently each time. This will naturally happen due to random rolls of the dice, changes the Crew makes to their plan, and the advantage they earn through practice. However, it also falls on you as GM to make sure that players encounter new obstacles each loop. For this reason, be sure to have more obstacles prepared than they can possibly get through in the first or second loops. Weve found that 1216 total obstacles is a good target number for a scenario.`,
            `Although the loops should build, its also critical you dont break the sense that it really is a time loop. Things the Crew hasnt interacted with shouldnt change from one loop to the next. If a guard makes a certain round during the first loop, she should make the same round during the second loopunless, of course, the players have done something that could plausibly interfere with this. This is how you balance the need for change with the need for continuity. Even just slightly different timing on the part of the players can be an excuse for you to mix things up. In addition, whenever the players fail a roll, theyll fail in a different way than they did before, which can create a new chain of events.`,
            `Try to limit the number of new obstacles introduced in the final loop to just a couple, mainly one or two complications for getting out. If everything seems to be going fairly smoothly in the final loop, perhaps the cops show up right as theyre escaping, and you could end the game with an epic car chase.`,
            `Since they get three loops, players shouldnt be too worried about messing things up. Let them try things out, learn and gain experience, and enjoy the ride!`
        ]
    },
    {
        title: "Obstacles",
        paragraphs: [
            `Both as youre planning and as youre running the game, remember that your job is not to come up with a story. Let your players surprise you! A game of Shenan-Again is always full of twists and turns that no one at the table couldve predicted, and that includes you as the GM. A story is the result of characters pursuing a goal and running into obstacles which impede them in that pursuit. This creates conflict, which is resolved by the choices the characters make.`,
            `As GM, youll give the players their goal and present obstacles, but theyre in charge of choosing how to deal with the obstacles in front of them. Thats what creates the storytheir choices. So when youre thinking about obstacles, avoid the temptation to think of solutions. Both you and the players will have much more fun if you discover the story together, rather than trying to force the players along on a story you came up with. Everyone will have more fun if the players have to rely on each other, rather than you.`,
            `Each player should also have a chance to be in the spotlight. In general, wed suggest you avoid outlining specific solutions to obstacles for your players, but if you notice a player hasnt rolled dice recently or seems less engaged, suggest to the group ways that this player could help solve a particular problem. Make sure everyone gets to participate!`,
        ]
    },
    {
        title: "Challenge Rolls",
        paragraphs: [
            `In order to overcome obstacles, your players will make Challenge rolls. As your players move through the game, a good rule of thumb is to make sure that they are rolling Challenges every three to five minutes. The timer itself should motivate the players to keep moving things along, but if things start to drag, throw in a new obstacle or complication to keep up the pacing. Basically, if youre not sure whether you should call for a roll on a certain action, you should probably call for a roll. Its more fun! Even if there doesnt seem to be much chance of failure with a particular action, theres always a way it could go wrong.`,
            `Now, obviously dont call for a roll to open unlocked doors or walk down unguarded hallways. But also, if you have too many unlocked doors and unguarded hallways, youre probably doing something wrong. A game of Shenan-Again should be challenging and constantly subvert the players expectations.
            Your goal as GM is for the players to win, but you dont want to make it easy for them. A perfect game of Shenan-Again will be up in the air until the last minute or two, or even the last thirty seconds. To create that tension, there needs to be a real fear of failure, so if youre in that third loop and the players are clipping along a little too fast, throw a wrench in their plans by introducing a new complication.`
        ],
        subsections: [
            {
                title: "Failing a Challenge Roll",
                paragraphs: [
                    `In a good game of Shenan-Again, players never know what might happen when they attempt their rolls. This uncertainty is part of the magic and adventure of the game. A great way to ensure that your players feel that magic and to keep them on the edge of their seats is keeping the complications of failure interesting.`,
                    `Think about a story you enjoy, whether thats a TV show, book, or movie. When the the protagonist tries something and fails, does it ever just not work? Chances are, failure adds some kind of complication which makes the story more interesting and drives it forward. Thats how a complication for a failed roll in Shenan-Again should feel.`,
                    `The results of failure should always be unpredictable. You cant plan how youre going to fail, right? If you succeed, that usually means things go about how you planned, but if you fail, that means things play out in a way you couldnt have anticipated. This forces you to adapt and keeps you on your toes.`,
                    `Make sure that your complication always requires the players to take a new approach, rather than just rolling for the same thing again. To help you think of different types of complications, weve broken them down into three types, but there are infinite different ways things could go wrong. These are just a few ideas:`,
                    `Yes but  The player who was rolling technically succeeds, but something they hadnt intended also happens. This still counts as a failure when it comes to calculating advantage, and complications from this are treated just like any other failure.`,
                    `Instead  The player meant to do one thing, but instead, they end up doing something very different.`,
                    `Seems like it worked.  This can be fun, but use it sparingly. In these cases, the player knows they failed the roll, but you just smile and tell them that it seems like it worked. In your head, youve thought of something that went wrong, but you dont tell the players yet. Instead, just leave them guessing about what might have happened, and keep that reveal for a bit later down the road.`,
                    `Avoid having the complication from a failed roll be You get caught/captured. Thats kinda boring, unless it moves the story forward somehow. Just having them locked up in an empty room isnt interesting, but if that locked room is also occupied by some poor minion whos displeased the boss, or it has a broken floorboard that leads down into the basementnow thats interesting.`,            
                ]
            },
            {
                title: 'Failed Roll Examples',
                paragraphs: [
                    `Example 1: the Hacker tries to access the elevators, but fails his roll. Maybe all the elevators stop where they are, and the doors open directly into the elevator shafts. Or perhaps the elevators start going up and down constantly to random floors. Or it works, but unbeknownst to the Hacker, someone noticed him on accessing their system and is now silently tracking his location.`,
                    `Example 2: The Con tries to gain someone's trust. Maybe the person become suspicious. Perhaps they arent suspicious at all, but still do something TOTALLY different from what the Con wants. Or it seems like it works, but the person is just playing along and watching the Con closely to figure out whats going on.`,
                    `Example 3: The thief tries to pick a lock. Maybe they take too long and someone notices before they can finish, or perhaps they unlock the door, but when they step through the door closes and locks behind them. Or they pick the lock, but unknowingly trigger an alarm.`,
                    `Example 4: The Muscle tries to knock someone down. Maybe the person knocks them down and take off running. Or maybe they dodge the blow and call in backup. Or they're knocked unconcious, but let out a yell before they go down to alert others nearby.`,
                ]
            },
        ]
    },
    {
        title: `Timestamps`,
        paragraphs: [
            `Often, it is advantageous to be aware of precisely when certain events happen. If the Crew wants to take advantage of a moment when a security guard takes a bathroom break, theyll want to know exactly when that happens. From a practical standpoint, no ones memory is perfect. As GM, you're only obligated to match times if the Scribe took a note with the timestamp, so the Crew cant count on an event occurring at a specific time unless they have it written down.`,
            `This is where the Scribe comes in. In addition to any other information that the players would like to keep track of, its very important that the Scribe note down the timestamps of key events. Remember that any timestamps for events caused by the Crew are subject to change, unless everything is done in exactly the same way.`,

        ]
    },
    {
        title: 'Planning and Taking Turns',
        paragraphs: [
            `As soon as the timer starts, the players should begin planning. During the first loop players should focus on how theyll get in, and make a more general plan. In the second and third loops theyll refine and perfect that plan based on their experience. The leader takes charge during planningtheyll mediate between players with differing approaches and decide on the best course of action for the Crew. Dont take too much time on this stepits much better spent just going for it. While its not entirely realistic, its way more fun if the Crew just kinda wings it. Doing <b>something</b> is always more fun than sitting around deliberating. The Crew will want to take a moment at the beginning of each new loop to reassess the planbut againdont let it eat up too much time. Remember, youre on the clock! `
        ],
        subsections: [
            {
                title: `Taking turns`,
                paragraphs: [
                    `Theres no set turn order in Shenan-Again. Instead, its up to the Crew to switch from one crewmember to another depending on what needs to happen next. In-world, your characters are all equipped with earpieces, so you can  communicate with one another in real time. You can use this fact to make suggestions about who should go next and how you  deal with obstacles that arise. Bring those suggestions to your crews leader (not the Gamemaster) because the leader calls the shots,they decide who goes when. The leader should make decisions quickly, as deliberating too long will eat up precious time.`,
                    `If youre the leader, wherever possible, try to spread tasks out evenly between the players so that everyone can contribute and have a good time. Even if youre not the leader, you can help out by noticing if one players being left outsuggest ways to put their skills to use. It can be helpful at times to have players pair up and work together, especially if you have a larger group. These pairs are fluid from one minute to the next, subject to adaptation based on the needs of the heist.`,
                    `A lot of situations rely on a sequence of events. First the Conman distracts a guard, then the Burglar sneaks past. But sometimes youll want two players to do something at the same time. In this case, you still choose one player to roll first, but rather than resolving any of their recovery rolls if they fail, (see Failing a Roll), immediately switch to the other player. Once theyve both made their first roll, you can keep resolving their failures and/or let other players take turns based on the situation.`,
                ]  
            },
            {
                title: `Gameplay Example:`,
                paragraphs: [
                    `Aaron, Liz, Maggie, and Sam are playing a scenario in which their crew attempts to steal a priceless statue from an archeological dig. Liz, the Muscle, is also the Crewleader.`,
                    `She says, Alright guys, howre we gonna get in?`,
                    `Maggie, the Burglar, suggests that she could sneak over to get a closer look.`,
                    `Go for it, Liz says.`,
                    `Maggie rolls to see if she can sneak up closer, and she succeeds. The GM tells her that theres a fence around the perimeter, and they check everyones ID at the gate. Sam, the Con, suggests that Maggie steal someones badge so he can use it to get in. Aaron, the Hacker, suggests that instead they dig their way under the fence.`,
                    `Liz thinks about it for a second and decides that it would be useful to have a badge once theyre inside, but thinks it might be too risky to steal one. Instead, she tells Sam to casually snap a picture of someones badge as theyre walking in or out, and plans to have Aaron use photo editing software to create a fake ID badge.`
                ]
            },
        ]
    },
    {
        title: `Two Kinds of Dice`,
        paragraphs: [
            `Not everything attempted should automatically succeed. Many times while attempting to do something very difficult, there will be limited time or limited information. To reflect these difficult tasks, where theres a real chance of failure and where failure would throw a complication in the Crews plan, the GM will call for the player to perform a Challenge roll, which determines if they succeed or fail. There are two types of dice in Shenan-Again which represent different kinds of resources available to the Crew: Luck Dice, and Skill Dice.`
        ],
        subsections: [
            {
                title: `Luck Dice`,
                paragraphs: [
                    `During set up,  twenty dice should be set in the middle of the table. This is a shared pool of dice called Luck Dice. If you dont have twenty dice, you keep track of your pool of dice on a piece of paper, crossing off dice when theyre used instead of physically removing them. Whenever players are called upon to roll for a Challenge, they can roll as many Luck Dice as they wish to overcome that Challenge. As long as they roll a six on at least one die, they succeed. This doesnt mean adding up the results. Look to see if <b>at least one die comes up as a six</b>. If they don't roll a single six, they fail. Whatever Luck Dice used, once rolled, are removed from play for the remainder of the current loop. At the beginning of each loop, the pool of Luck Dice resets back to twenty.`,
                    
                ]
            },
            {
                title: `Skill Dice`,
                paragraphs: [
                    `If players are rolling a Challenge thats related to their Role (say the Hacker is trying to disable some security cameras) they add two Skill Dice to any number of Luck Dice. In this case, they must roll at least one six on any one of the dice, regardless of which type of die it is. If they wish, they can just roll your two Skill Dice with no Luck Dice, though the odds of rolling a six on just the Skill Dice are fairly slim. If the Challenge doesnt relate to their Role, but they dont want to waste any Luck Dice on it, they can always roll just one Skill Dice alone. Whatever dice theyll be rolling, <b>they need to decide beforehand which dice, and how many, and roll all of them at once.</b>`
                ]
            },
            {
                title: `Skill Dice Examples:`,
                paragraphs: [
                    `
                    <ul>
                        <li>The Burglar could add their Skill Dice to pick a lock, but not to knock someone out</li>
                        <li>The Hacker could add their Skill Dice to access an elevators control panel, but not to lie to someone</li>
                        <li>The Con could add their Skill Dice to pass themselves off as an employee of the people youre robbing, but not to crack a safe.</li>
                        <li>The Muscle could add their Skill Dice to shoot someone with a sleeping dart, but not to decrypt a secure email</li>
                    </ul>
                    `
                ]
            }
        ]
    },
    {
        title: `Gaining Advantage`,
        paragraphs: [
            `Because theyre in a time-loop, the Crew has a distinct edge in that they relive the same moments multiple times, allowing them to predict what will happen before it occurs and adjust accordingly. Hopefully, theyll adapt their plan to avoid the mistakes they made in their past attempts. Thats one way that the time-loop gives the Crew an edge. Another way thats shown is Advantage. Because theyre in a time-loop, the Crew has a distinct edge in that they relive the same moments multiple times, allowing them to predict what will happen before it occurs and adjust accordingly. Hopefully, theyll adapt their plan to avoid the mistakes they made in their past attempts. Thats one way that the time-loop gives the Crew an edge. Another way thats shown is Advantage.`,
            `Under normal conditions, at least one <b>six</b> is needed to succeed at a roll. However, if the player attempted the exact same Challenge roll in a previous loop, they will succeed as long as they roll a five <b>or</b> six, even if they failed before.`,
            `If they <b>succeeded</b> at least once on the same Challenge in a previous loop, they succeed on a roll of a <b>four</b>, five, or six. For this higher level of Advantage, it doesnt matter whether they succeeded in the last loop, or how many times they succeeded. Maybe its the second loop, and the player succeeded in the first loop. Maybe its the third, and they succeeded on the first but failed on the second, or maybe they succeeded both times. Regardless, as long as theyve succeeded <b>at least once</b> at doing <b>exactly</b> the same thing, theyll succeed on a four, five, or six.`,
            `Players will only have Advantage when theyve previously attempted the <b>exact same thing</b>. In many cases, this means itll have to be in the same room, in the same situation. It doesnt necessarily have to be the exact same time, but the circumstances need to be  identical. Its not just a question of having picked <b>any</b> lock, its whether theyve picked that <b>specific</b> lock before. When it comes to people, its not just whether theyve tackled <b>that</b> guard before, its whether theyve done it in the same space in basically the same situation.`
        ],
        subsections: [
            {
                title: `Advantage Gameplay Example:`,
                paragraphs: [
                    `Aaron, Liz, Maggie, and Sam have infiltrated the dig site. Theyre on the second loop. Liz, the Muscle and the leader, instructs Sam to find out who has the key to the bunker where the artifacts are stored. He strikes up a conversation with one of the people working the site.`,
                    `Sams had this conversation before. He didnt succeed in getting this info last time, but this time at least he knows what not to say. The GM, Matt, calls for a challenge roll. Youre on fives or sixes, he reminds Sam.`,
                    `Sam figures the info hes fishing for is important, so he takes three Luck Dice to get pretty good odds. Maggie reminds him to add his Skill Dice, since this is clearly related to his Role as the Con.`,
                    `Sam smiles. How could I forget?`,
                    `He rolls 4, 3, 1, 5, and 5. Since he rolled at least one five or six, he succeeds this time. They get the info they need, and proceed with the next step in their plan.`
                ]
            }
        ]
    },
    {
        title: `Crafting Scenarios`,
        paragraphs: [
            `A Shenan-Again Scenario should include 4-5 non-player characters (NPCs), a brief description of the situation facing the Crew, and a list of 12-15 prepared obstacles. Its better to over-prepare than to under-preparewhile you might not use every obstacle you prepped, youll be glad you had them. Think of the list of obstacles more like a toolkit, rather than the outline of a plot. The plot will come as a result of the choices the players make. As they begin to make choices, give them obstacles as needed to build the tension and keep things interesting.`,
            `When presenting the Scenario to the players, describe the premise and the Crews goal, tell the Crew about the initial obstacles, and introduce NPCs as seems appropriate. Feel free to use the same format described in Part 2  The Crew for creating character Concepts to create Concepts for the NPCs; its a convenient way to quickly come up with distinct characters.`,
            `The Shenan-Again game box comes with pre-built Scenarios, and each Scenario includes a map that can be used for reference during gameplay. When designing your own Scenarios, you might find it helpful to sketch the map or print one out, so the players can have a visual for whats going on.`
        ]
    },
    {
        title: `Some Final Words`,
        paragraphs: [
            `Theres something magical when you bring a series of obstacles to the table, and the players come up with solutions to those obstacles that you never would have dreamed up in a million years. Its hilarious, spontaneous, and incredibly fulfilling. Its not just an act of creation, its an act of discovery. No one knows exactly whats going to happen. The players dont know what youll throw at them, you dont know how the players will react, and neither of you know how the dice will fall. Its a thrilling experience, and we hope you create some unforgettable memories as you play Shenan-Again.`
        ]
    }
